.App {
	text-align: center;
}

.Tile-layout {
	width: 80%;
	margin: 2% auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	background: rgba(36, 66, 114, 0.8);
	border: solid 5px #000;
	border-radius: 25px;
	-webkit-box-shadow: 10px 10px 2px 0px rgba(0,0,0,0.3);
	        box-shadow: 10px 10px 2px 0px rgba(0,0,0,0.3);
}

.footer {
	background: rgba(255,255,255,0.1);
	padding: 0.5em 0;
	margin-top: 2em;
}


