.tile {
	margin: 2% 4%;
}

.tile-img {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.tile-img img {
	border: solid 3px #000;
	border-radius: 50%;
	-webkit-box-shadow: 6px 6px 2px 0px rgba(0,0,0,0.3);
	        box-shadow: 6px 6px 2px 0px rgba(0,0,0,0.3);
	-webkit-transition-duration: 500ms;
	     -o-transition-duration: 500ms;
	        transition-duration: 500ms;
}

.tile-img img:hover {
	-webkit-box-shadow: none;
	        box-shadow: none;
	position: relative;
	bottom: -2px;
	right: -2px;
}

.tile-img img:active {
	opacity: 0.6;
}

.hint {
	opacity:0.3;
}


